$glass: rgba(255, 255, 255, 0.2);
$glass-icon: rgba(255, 255, 255, 0.3);
$gradient: linear-gradient(35deg, red, purple);
$option: #320a28;

 body{
  background: $gradient no-repeat;
  height : 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  overflow: visible;
}

hr{
  border :none;
  width:60%;
  position: relative;
  left : 0.8em;
  bottom:1.5em;
  border-top: 1px hotpink dashed;
}

.up{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;

}

.down{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}

.text_box{
  font-size: 24px;
  position: relative;
  left:1.3em;
  width:65px;
  border:none;
  background-color: none;
  background: none;
  outline:none;
  border-bottom: 2px solid hotpink;
  text-shadow: 0 0 10px #fff, 0 0 80px #ff00de, 0 0 150px #ff00de; 
  font-family: "Architects Daughter";
  text-align: center;
  color:white;
}

.text_box:focus{
  border-bottom: 2px solid rgb(153, 8, 85);
}



.text{
  // font-family:'Courier New', Courier, monospace;
  font-family: "Gloria Hallelujah";
  color:#FFEAEA;
  text-align:center;
}

.text1{
  // font-family: 'Courier New', Courier, monospace;
